.container
{
  margin:0 auto;
  width:100%;
  height:100%;
  padding:0 0 0 5%;
}
.loadinglist
{
  width:1000px;
  position:relative;
  margin:0 auto;
  list-style:none;
  padding:0;
}
.loadinglist li
{
  float:left;
  margin:5px 0 0 5px;
  overflow:hidden;
}
.loadinglist li #panel
{
  display:block;
  height:180px;
  width:300px;
  box-shadow:0 0 20px #333;
  -mozbox-shadow:0 0 20px #333;
  -ms-box-shadow:0 0 20px #333;
  -o-box-shadow:0 0 20px #333;
  -webkit-box-shadow:0 0 20px #333;

  border:1px solid #333;

  border-radius:8px;
  -moz-border-radius:8px;
  -webkit-border-radius:8px;
  -ms-border-radius:8px;
  -o-border-radius:8px;
}
.loadinglist li #panel:hover
{
  border-color:#444;
}
